import React, { Component } from 'react';
import './WorkoutContainer.css';

import WorkoutCard from '../components/WorkoutCard';

class WorkoutContainer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      totalTime: 0,
      numCardio: 0,
      numLegs: 0,
      numChest: 0,
      numBack: 0,
      numArms: 0,
    }
  }


  clicked = (passedProps) => {
    this.props.clicked(passedProps);
  }

  getCards = () => {
    let cards = [];
    for (let i = 0; i < this.props.exercises.length; i++) {
      let {name, time, category} = this.props.exercises[i];
      cards.push(
        <WorkoutCard
            key={name}
            name={name}
            time={time}
            category={category}
            clicked={this.clicked} />
      );
    }
    return cards;
  }

  createSummary = () => {
    let totalTime = 0;
    let numCardio = 0;
    let numBack = 0;
    let numLegs = 0;
    let numArms = 0;
    let numChest = 0;

    for (let i = 0; i < this.props.exercises.length; i++) {
      totalTime += this.props.exercises[i].time;
      switch(this.props.exercises[i].category) {
        case 'Chest':
          numChest++;
          break;
        case 'Legs':
          numLegs++;
          break;
        case 'Arms':
          numLegs++;
          break;
        case 'Back':
          numBack++;
          break;
        case 'Cardio':
          numCardio++;
          break;
        default:
          break;
      } 
    }
    this.setState({
      totalTime: totalTime,
      numCardio: numCardio,
      numBack: numBack,
      numLegs: numLegs,
      numArms: numArms,
      numChest: numChest,
    })
  }

  componentWillReceiveProps = (nextProps) => {
    this.createSummary();
  }


  render() {
    let cards = this.getCards();
    return (
      <>
        <div className="workout-summary-container">
          Total Time: {this.state.totalTime} minutes
          <br />
          Chest: {this.state.numChest}
          <br />
          Legs: {this.state.numLegs}
          <br />
          Arms: {this.state.numArms}
          <br />
          Back: {this.state.numBack}
          <br />
          Cardio: {this.state.numCardio}
        </div>
        <div className="workout-container">
          {cards}
        </div>
      </>
    );
  }
}

export default WorkoutContainer;